<template>
    <transition name="fade" appear>
        <main class="content">
            <div class="mv_img" v-if="article.mainimage">
                <img :src="article.mainimage.url" />
            </div>
            <h1 v-cloak>{{ article.title }}</h1>
            <div class="date">
                {{ new Date(article.publishedAt).toLocaleDateString() }}
            </div>
            <div class="article_main" v-html="maintext"></div>
        </main>
    </transition>
</template>

<script>
import axios from "axios";
import cheerio from "cheerio";
import hljs from "highlight.js";
import "highlight.js/styles/hybrid.css";

export default {
    name: "ArticleDetail",
    data() {
        return {
            article: {},
            maintext: {},
        };
    },
    mounted() {
        this.getArticle();
    },
    methods: {
        async getArticle() {
            // id
            const response = await axios.get(
                process.env.VUE_APP_X_API_URL_BLOG + this.$route.params.id,
                {
                    headers: {
                        "X-API-KEY": process.env.VUE_APP_X_API_KEY,
                    },
                }
            );
            this.article = response.data;
            const $ = cheerio.load(response.data.body);
            $("pre code").each((_, elm) => {
                const result = hljs.highlightAuto($(elm).text());
                $(elm).html(result.value);
                $(elm).addClass("hljs");
            });
            this.maintext = $.html();
            //title
            document.title = this.article.title;
        },
    },
};
</script>
